* {
    box-sizing: border-box;
    margin: 0;
  }
  
  :root {
    --heights: 300px;
    --widths: 100%;
  }

  section{
    height: 55vh;
  }
  
  .slider-container {
    height: var(--heights);
    width: var(--widths);
    position: relative;
    margin: auto;
    overflow: hidden;
  }
  
  .active {
    display: inline-block;
  }
  
  .inactive {
    display: none;
  }
  
  .slides {
    height: 55vh;
    width: var(--widths);
    position: relative;
  }
  
  .slide-image {
    width: 100%;
    height: 55vh;
    position: absolute;
    object-fit: cover;
  }

  .slider-parent{
    width: 100%;
    height: 55vh;
    display: flex;
    justify-content: center;
  }

 .slider-text{
    width: 420px;
    height: 260px;
    top: 27%;
    position: absolute;
    border-radius: 10px;
    background-color: rgba(0, 0, 0, 0.716);
  }

  .slide-title{
    width: 100%;
    height: 100%;
    color: rgb(3, 186, 210);
    font-size: 45px;
    position: absolute;
    text-align: center;
    top: 20%;
    right: 0%;
  }
  
  .slide-text {
    width: 82%;
    height: 40%;
    color: rgb(250, 250, 250);
    font-size: 26px;
    position: absolute;
    text-align: center;
    top: 50%;
    right: 9%;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  }
  
  .prev,
  .next {
    color: transparent;
    cursor: pointer;
    position: absolute;
    top: 27%;
    width: auto;
    padding: 1rem;
    margin-top: -3rem;
    font-size: 40px;
    font-weight: bold;
    border-radius: 0px 5px 5px 0px;
  }
  
  .hero:hover .prev,.hero:hover .next {
    color: black
  }
  
  .hero:hover .prev:hover,
  .hero:hover .next:hover {
    color: white;
    background-color: rgba(0, 0, 0, 0.6);
    transition: all 0.5s ease-in;
  }
  
  .next {
    right: 0;
    border-radius: 5px 0px 0px 5px;
  }
  
  .all-dots {
    width: 0%;
    height: 0%;
    position: absolute;
    display: flex;
    justify-content: center;
    z-index: 200;
  }
  
  .dot {
    cursor: pointer;
    height: 1.5rem;
    width: 1.5rem;
    margin: 0px 3px;
    background-color: transparent;
    /* background-color: rgba(0, 0, 0, 0.3); */
    border-radius: 50%;
    display: none;
  }
  
  .slider-container:hover .dot:hover {
    background-color: rgba(255, 255, 255, 0.5);
  }
  
  .active-dot {
    background-color: rgba(255, 255, 255, 0.5);
  }
  
  .slider-container:hover .dot{
    background-color: rgba(0, 0, 0, 0.3);
  }
  .slider-container:hover .active-dot{
    background-color: rgba(255, 255, 255, 0.5);
  }
  
  .play-pause {
    color: black;
  }