@import url('https://fonts.googleapis.com/css2?family=Roboto+Flex:wght@400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap');

//CSS Reset
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    font-family: 'Roboto Flex', sans-serif;
    background-color: white;
    overflow-x: hidden;

    a{
        text-decoration: none;
    }
    li{
        list-style: none;
    }
    button{
        border: none;
        outline: none;
    }
    .text-center{
        text-align: center;
    }
}

div .loading-gif{
    width: 100%;
    margin-top: -250px;
    z-index: 10;
    position: absolute;
}

//Header CSS


    .header{
        position: absolute;
        width: 100%;
        z-index: 10;
        height: 75px;

        .logo{
            width: 9rem;
            height: 3.5rem;
            margin: 5px 0px 0px 12px;
            transition: all ease-in .5s;
            cursor: pointer;
        }
        .menu-icon{
            width: 40px;
            height: 40px;
            margin: 16px 8px 0px 0px;
            cursor: pointer;
            position: absolute;
            right: 8px;
        }
        }
        .no-icon{
            display: none;
        }
        .close-icon{
            width: 35px;
            height: 35px;
            margin: 16px 8px 0px 0px;
            cursor: pointer;
            position: absolute;
            right: 8px;
        }

    .show-items{
        height: 36.5vh;
        background-color: rgb(35, 37, 54);
        animation: slideIn 0.7s;
        margin: -65px 0 0 0;
        padding-top: 90px;

        @keyframes slideIn {
            0%{
                transform: translate(0px, -100%);
            }
            100%{
                transform: translate(0px, 0%);
            }
        }

        a{
            width: 105px;
            display: block;

          li{
                display: flex;
                padding: 8.5px 0px;
                margin: 0px 0px 0px 32px;

                h2{
                    color: white;
                    font-size: 22px;
                    font-weight: 600;
                    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
                    transition: all ease-in 0.3s;

                    &:hover{
                        color: rgb(131, 208, 231);
                    }
                }
                img{
                    margin-right: 8px;
                    width: 22px;
                    height: 22px;
                }
            }
        }
    }

    .hide-items{
        display: none;
    }

//************Hero & Card CSS*************\\

.hero{
    background-color: white;
    height: auto;

    .search-div{
        display: flex;
        height: 38px;
        width: 43%;
        position: absolute;
        left: 30%;
        top: 2.5%;
        z-index: 11;

        .search-bar{
            width: 88%;
            padding-left: 8px;
            font-size: 15px;
            border: none;
            outline: none;
            border-radius: 6px 0px 0px 6px;
        }

        .search-button{
            width: 55px;
            border-radius: 0px 6px 6px 0px;
            background-color: rgb(134, 134, 134);
            color: white;
            cursor: pointer;
            transition: all ease-in .3s;
            position: relative;

            img{
                position: absolute;
                top: 5px;
                left: 8px;
                width: 28px;
            }

            &:hover{
                background-color: rgba(66, 66, 66, 0.958);
            }
        }
    }
 
    .product{
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
        grid-gap: 35px;
        margin: 0 30px 0 30px;
        padding-bottom: 30px;
        min-height: 312px;
        padding-top: 60px;

        .product-wall-parent{
            position: absolute;
            width: 95.5%;
            height: 5vh;
            top: 50%;
            text-align: center;
            display: flex;
            justify-content: center;

            .product-wall{
                position: absolute;
                top: -33.5%;
                width: 300px;
                height: 85px;
            }
            .cart-btn{
                display: flex;
                position: absolute;
                right: -3%;
                top: 0%;
                margin-right: 35px;            
                padding: 11px 26px;
                background-color: rgb(40, 40, 62);
                border-radius: 7px;
                cursor: pointer;
                margin-top: 40px;

                &:hover{
                    background-color: rgb(31, 31, 108);
                }

                h3{
                    font-size: 20px;
                    color: white;
                }
                img{
                    width: 25px;
                }
                .cart-count{
                    position: absolute;
                    color: rgb(21, 0, 255);
                    left: 70%;
                    top: 22%;
                    padding: 0 3px;
                    font-size: 14px;
                    background-color: aqua;
                    border-radius: 10px;
                }
            }
        }

        li{
            height: 315px;
            width: 260px;
            border-radius: 8px;
            background-color: rgb(53, 64, 82);
            position: relative;
            box-shadow: 3px 3px 10px #000000;
            transition: .5s;
            margin: 0 auto;

            &:hover{
                transform: scale(1.08);
            }

            img{
                height: 240px;
                width: 260px;
                object-fit: cover;
                border-radius: 10px 10px 0 0;
                position: relative;
                cursor: pointer;
            }
            h5{
                position: absolute;
                top: 68%;
                right: 1%;
                color: white;
                background-color: rgb(46, 46, 46);
                padding: 3px 6px;
                border-radius: 5px;
            }
            .title{
                color: white;
                font-size: 21px;
                margin-top: 5px;
                margin-left: 18px;
                font-family: Arial, Helvetica, sans-serif;
                max-height: 28px;
                overflow: hidden;
            }

            .cart-btn{
                display: flex;

                .price{
                   color: white;
                   font-size: 19px;
                   font-weight: 500;
                   position: absolute;
                   left: 6%;
                   margin-top: 10px;
                   font-family: 'Times New Roman', Times, serif;
                }
                .price-icon{
                   height: 30px;
                   width: 30px;
                   position: absolute;
                   left: -1.5%;
                   margin-top: 17px;
                }

                button{
                    padding: 9px 18px;
                    border-radius: 6px;
                    outline: none;
                    cursor: pointer;
                    position: absolute;
                    left: 55%;
                    color: white;
                    font-weight: 700;
                    background-color: #3387fb;
                    z-index: 9;
                    margin-top: 4px;

                   &:hover{
                     background-color: #0f6ef3;
                   }
               }
            }
        }}}

.popup{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #0c0c0cac;
    z-index: 10;
    height: 10426px;

    .popup-content{
        position: fixed;
        height: 500px;
        width: 450px;
        background-color: rgb(180, 247, 253);
        transform: translate(-50%, 25%);
        left: 50%;
        border-radius: 10px;
        box-shadow: 5px 5px 20px black;

        .close-icon{
            width: 35px;
            margin-right: 8px;
            cursor: pointer;
            position: absolute;
            left: 100%;
            top: -3%;
        }

        .images-list{
            position: absolute;
            width: 75%;
            height: 75%;
            top: 12%;
            left: -36%;
            border-radius: 10px;
            box-shadow: 0.5px 0.5px 5px rgb(147, 145, 145);
            background-color: white;

            .product-thumbnail{
                border-radius: 10px 10px 0 0;
                width: 100%;
                height: 80%;
            }

            .sub-images-list{
                display: flex;
                margin-top: 5px;
                margin-left: 22px;

                .product-images{
                    width: 54px;
                    height: 54px;
                    border-radius: 5px;
                    margin-right: 20px;
                    border: 1px solid rgb(211, 211, 211);
                    box-shadow: .5px .5px 1px rgb(124, 124, 124);
                }
            }
          }
        .text-list{
            margin-left: 182px;
            margin-top: 30px;
            width: 260px;

            .product-title{
                font-size: 26px;
                min-height: 40px;
                text-align: center;
                color: rgb(42, 40, 76);
                font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
            }
            .title-line{
                padding: .1px 1px;
                margin: 12px 10px;
                background-color: black;
            }
            .product-category{
                position: absolute;
                top: 67.5%;
                left: 17%;
                font-size: 13px;
                background-color: rgba(50, 57, 114, 0.853);
                padding: 1px 8px;
                border-radius: 8px;
                color: white;
            }
            .product-brand{
                font-size: 14.5px;
                text-align: center;
                margin: 5px;
                color: rgb(41, 46, 82);
                font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
            }
            .price-quantity-div{
                display: flex;
                margin: 25px 15px 15px 15px;
                text-align: center;
                width: 185%;

                h5{
                    font-size: 16px;
                    color: rgb(68, 68, 68);
                }
                .price-div{
                    width: 50%;

                    .product-price{
                        font-size: 27px;
                        color: rgb(248, 60, 92);
                        margin: 5px 0 12px 0;
                        margin-left: -10px;
                    }
                }
                .quantity-div{
                    width: 50%;

                    .product-quantity{
                        margin: 10px 0;
                        width: 100%;
                        height: 30px;
                        border-radius: 20px;
                        background-color: white;
                        display: flex;

                        img{
                            cursor: pointer;
                        }
                        .count{
                            width: 55px;
                            text-align: center;
                        }
                    }
                }
            }

            .product-stock{
                color: green;
                text-align: center;
            }

            .product-description{
                font-size: 16px;
                font-weight: 500;
                margin: 25px 15px 15px 15px;
                min-height: 70px;
                text-align: center;
            }
        }
        .btn-parent{
            text-align: center;

        .add-btn{
            padding: 15px 24px;
            font-size: 17px;
            font-weight: 600;
            color: white;
            border-radius: 7px;
            cursor: pointer;
            margin-top: 30px;
            background-color: rgb(221, 57, 178);
            transition: all ease 0.3s;

            &:hover{
                background-color: rgb(255, 0, 187);
            }
        }}
    }
}

//AddToCart CSS

.add-to-cart-wrapper{
    font-family: 'Poppins', sans-serif;
    overflow: hidden;
    height: 100vh;

    .background-color{
        background-color: rgb(35, 37, 54);
        height: 80px;
    }

    header {
        width: 200%;
        margin-bottom: 2rem;

        .header-bar{
            display: flex;
            align-items: center;
            margin: 20px 120px;
            width: 41%;
            padding-bottom: 15px;
            border-bottom: 1px solid #818181;;

            .arrow-icon{
                width: 16px;
                height: 14px;
                cursor: pointer;
            }
            h3{
                font-size: 16px;
                text-transform: capitalize;
                color: #807e7e;
                margin-left: 10px;
                font-weight: 400;
            }
            .cart-icon{
                width: 40px;
                position: absolute;
                right: 10.5%;
            }
            .count{
                position: absolute;
                right: 10.1%;
                padding: 2px 6px;
                color: white;
                font-size: 12px;
                border-radius: 15px;
                margin-bottom: 24px;
                background-color: rgb(29, 120, 189);
            }
        }
    }

    .add-to-cart-items{
        margin: 20px 120px;
        height: 77vh;
        
        h2{
            font-size: 22px;
            font-weight: 500;
        }
        p{
            font-size: 13px;
            margin: 3px 0;
            color: #545454;
        }

        .cart-items{
            background-color: #f1f1f174;
            height: 57vh;
            width: 97.5%;
            margin: 20px 0;
            box-shadow: 1px 1px 5px #bdbcbc;
            padding: 20px 40px;
            overflow-y: scroll;

            .cart-items-list{
                display: flex;
                align-items: center;
                height: 11vh;
                padding: 20px 0;
                border-bottom: 1px solid #949494;

                .cart-image{
                    width: 120px;
                    height: 65px;
                    margin: -12px 60px 0 50px;
                    margin-top: 0;
                }

                .product-title{
                    text-align: center;
                    width: 300px;
                    overflow: hidden;

                    .cart-title{
                        font-size: 18px;
                    }
                    .cart-category{
                        font-size: 13px;
                        font-weight: 500;
                }
            }

            .cart-quantity{
                display: flex;
                justify-content: center;
                width: 300px;

                .count{
                    margin: 0 10px;
                }
                img{
                    cursor: pointer;
                }
            }

            .cart-price{
                font-size: 19px;
                width: 200px;
                text-align: center;
                font-family: 'Times New Roman', Times, serif;
                font-weight: 600;
            }
            .delete-icon{
                margin-left: 70px;
                cursor: pointer;
            }
        }
        .empty-cart{
            width: 100%;
            height: 100px;
            text-align: center;
            margin-top: 9.5rem;

            h4{
                font-weight: 400;
                font-size: 12px;
            }
        }
    }
    .cart-total{
    
    h2{
        font-size: 19px;
        margin-left: 83%;
        margin-top: -10px;
        font-family: 'Times New Roman', Times, serif;
        font-weight: 600;
      }

    .checkout{
        margin-left: 90%;
        margin-top: 3px;
        background-color: #3387fb;
        padding: 6px 16px;
        border-radius: 5px;
        color: white;
        font-weight: 600;
        cursor: pointer;

        &:hover{
            background-color: #006aff;
        }
    }
   }
}
}

//Contact CSS
.container{
    width: 100%;

    .background-color{
        background-color: rgb(35, 37, 54);
        height: 80px;
    }


.contact{
    height: auto;
    width: 1050px;
    display: flex;
    margin: 0 auto;

    &-form{
        background-color: #112f49;
        height: 100vh;
        width: 570px;
        border-radius: 15px;
        margin: 40px 20px;
        color: white;

        h1{
            font-size: 25px;
            font-weight: 500;
            margin: 40px 0 0 30px; 
        }

        .send-btn{
            padding: 12px 20px;
            border-radius: 8px;
            font-size: 17px;
            font-weight: 200;
            background-color: #6969f6;
            border: none;
            outline: none;
            color: white;
            cursor: pointer;
            transition: all ease-in .3s;
            margin: -40px 0 30px 0;
    
            &:hover{
                background-color: #2626ec;
            }
        }

        .forms{
            margin: 35px;

            label h3{
                font-size: 18px;
                font-weight: 200;
                margin-bottom: 5px;
                color: white;
            }

            input{
                background-color: rgb(30 56 81);
                border: none;
                width: 100%;
                height: 45px;
                margin: 0 2px 20px 2px;
                border-radius: 8px;
                padding-left: 13px;
                font-size: 17px;
                color: white;
                border: 1px solid rgba(50, 74, 74, 0.689);
            }
            #message-input{
                height: 220px;
                
            }
        }
    }

    &-details{
        margin-left: 50px;
        margin-top: 60px;
        width: 400px;

        h1{
            font-size: 25px;
            font-weight: 500;
            margin-bottom: 40px;
            color: #112f49;
        }

        .details{
            display: flex;
            position: relative;
            margin-bottom: 20px;

            svg{
                color: #112f49;
                display: inline-block;
                position: absolute;
                height: 24px;
                width: 24px;
            }

            h3{
                margin-left: 40px;
                font-size: 18px;
                font-weight: 200;
                color: #112f49;
            }
        }

        .google-map{
            iframe{
                width: 300px;
                height: 400px;
                margin: 30px 0;
                border-radius: 10px;
            }
        }
    }
}
}

//About CSS

.about{
    width: 100%;
    border: 50px solid #232536;
    border-top: 0;

    .background-color{
        background-color: #232536;
        height: 80px;
    }

    .about-disclaimer{
        height: 83vh;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;

        img{
            width: 300px;
        }
    }
}